<template class="d-flex">
  <v-card elevation="0" class="mb-3">
    <v-card-text
      class="title font-weight-bold d-flex justify-center"
      style="padding-bottom:0"
    >第{{th}}天 {{date}}</v-card-text>
    <span class="d-flex justify-center">
      <span class="circle"></span>
    </span>
    <span class="d-flex justify-center">
      <span class="wp-sep clear-both"></span>
    </span>

    <div v-for="(item, i) in cards" :key="i">
      <v-card class="px-2 pt-2 pb-1 mx-2" elevation="1" max-width="720px" style="margin:0 auto"  @click="intocontent(item)">
        <div v-if="item.pic">
          <div class="photo-ctn" v-if="handelurl(item.pic)==='img'">
            <v-img
              :src="item.pic"
              :lazy-src="lazyimg"
              gradient="to right, rgba(0, 0, 0, 0.5) 0%, transparent"
            >
              <template v-slot:placeholder>
                <v-row class="fill-height ma-0" align="center" justify="center">
                  <v-progress-circular indeterminate color="grey lighten-5"></v-progress-circular>
                </v-row>
              </template>
            </v-img>
            <div class="wp-btns">
              <a class="comment-btn">
                <v-icon size="20" color="white" class="pt-1 mx-1 float-right">mdi-message</v-icon>
                <span>{{item.comments.length}}</span>
              </a>
              <i class="icon-btnbg"></i>
              <a :class="likeclass">
                <v-icon size="20" :color="likecolor" class="mx-1">mdi-cards-heart</v-icon>
                <span>{{item.likeUsers.length}}</span>
              </a>
            </div>
          </div>

          <div v-else>
            <d-player :options="urlRoptions(item.pic)"></d-player>
          </div>
        </div>

        <v-card-text>
          <div class="text--primary">{{item.content}}</div>
        </v-card-text>

        <v-row>
          <v-col cols="7" class="d-flex align-center">
            <v-icon size="20" class="mr-1">mdi-calendar</v-icon>
            {{item.date}}
            <span class="body-1 cla"></span>
          </v-col>
          <v-col cols="5" class="d-flex flex-row-reverse" v-if="item.location">
            <v-chip color="teal" text-color="white">
              <v-avatar>
                <v-icon size="18">mdi-earth</v-icon>
              </v-avatar>
              {{item.location}}
            </v-chip>
          </v-col>
        </v-row>
      </v-card>

      <span class="d-flex justify-center" v-if="i!=cards.length-1">
        <span class="wp-sep clear-both"></span>
      </span>
    </div>
  </v-card>
</template>

<script>
import { mapState } from "vuex";
import VueDPlayer from "vue-dplayer";
import "vue-dplayer/dist/vue-dplayer.css";

export default {
  components: {
    "d-player": VueDPlayer
  },
  props: {
    cards: {
      type: Array,
      required: true
    },
    th: {
      type: String,
      required: true
    },
    date: {
      type: String,
      required: true
    }
  },
  computed: {},
  data: () => ({
    img: "",
    likecolor: "white",
    likeclass: "like-btn",
    lazyimg:`${process.env.VUE_APP_IMGURL}img/lazyimg.jpg`
  }),
  methods: {
    intocontent(item) {
      this.$router.push({
        name: "Content",
        params: {
          cardId: item.id,
          card: item
        }
      });
    },
    handelurl(url) {
      if (typeof url === "string") {
        const ulist = url.split(".");
        const u = ulist[ulist.length - 1];
        if (u === "mp4") {
          return "video";
        } else {
          return "img";
        }
      } else {
        return null;
      }
    },
    urlRoptions(url) {
      return {
        video: {
          url: url,
          type: "normal"
        },
        autoplay: false,
        hotkey: true
      };
    },
  }
};
</script>

